.todo-list {
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 8px;
  flex-direction: column;
  max-width: 800px;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

.todo-list-head {
  justify-content: space-between;
  align-items: center;
  margin-bottom: .5rem;
  display: flex;
}

.todo-list-head span {
  color: #333;
  flex: 1;
  font-size: 1.2rem;
  font-weight: bold;
}

.todo-list-head label {
  color: #666;
  font-size: .9rem;
  font-weight: normal;
}

.todo-list-head label input[type="checkbox"] {
  vertical-align: middle;
}

.todo-list-task-input {
  gap: .5rem;
  margin-top: .5rem;
  display: flex;
}

.todo-list-task-input input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  padding: .5rem;
  font-size: 1rem;
}

.todo-list-task-input select, .todo-list-task-input input[type="date"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: .5rem;
  font-size: 1rem;
}

.todo-list-task {
  background-color: #fff;
  border-radius: 4px;
  align-items: center;
  gap: 1rem;
  margin-bottom: .5rem;
  padding: .8rem 1rem;
  display: flex;
  box-shadow: 0 1px 2px #0000001a;
}

.todo-list-description {
  flex: 1;
  font-size: 1rem;
}

.todo-list-completed .todo-list-description {
  color: #888;
  text-decoration: line-through;
}

.todo-list-priority {
  font-size: 1rem;
  font-weight: bold;
}

.todo-list-deadline, .todo-list-username {
  color: #888;
  font-size: .9rem;
}

.todo-list-task button {
  cursor: pointer;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: .5rem 1rem;
  font-size: .9rem;
  transition: background-color .3s;
}

.todo-list-task button:hover {
  background-color: #0056b3;
}

.todo-list-overdue {
  background-color: #fccfcf;
}

.error-message {
  color: #c62828;
  background-color: #ffcdd2;
  border: 1px solid #c62828;
  border-radius: 4px;
  margin-top: .5rem;
  padding: .5rem;
  font-size: .9rem;
}

.circle-blue {
  background-color: #3498db;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  display: inline-block;
}

.circle-orange {
  background-color: #f39c12;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  display: inline-block;
}

.circle-purple {
  background-color: #9b59b6;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  display: inline-block;
}

.authenticate-login {
  flex-direction: column;
  gap: .5rem;
  display: flex;

  & > label {
    gap: .3rem;
    display: flex;

    & > span:first-child {
      text-align: right;
      flex: 1 2 50%;
    }

    & > span:nth-child(2) {
      text-align: left;
      flex: 50%;
    }
  }

  & input {
    width: 8rem;
    font-size: 100%;
  }

  & > button {
    align-self: center;
  }
}

.authenticate-logout {
  align-items: center;
  gap: .3rem;
  margin-bottom: .5rem;
  display: flex;
}
